import { Component, OnInit } from '@angular/core';
import { TruckInfo } from 'src/app/types/truck-info';
import { TruckPropertyName } from 'src/app/types/truck-property-name.enum';
import { CompanyServicesService } from './company-services.service';

@Component({
  selector: 'app-company-services',
  templateUrl: './company-services.component.html',
  styleUrls: ['./company-services.component.scss']
})
export class CompanyServicesComponent implements OnInit {

  public trucks: TruckInfo[];
  public truckProperties = TruckPropertyName;
  public posibilities: string[];

  constructor(
    public companyServicesComponent: CompanyServicesService,
    ) { }

  ngOnInit(): void {
    this.trucks = this.companyServicesComponent.getTruckData();
  this.posibilities = this.companyServicesComponent.getPosibilitiesData();
  }

}
